.custom-body {
  background-color: #EDEDED;
}

.circular-menu {
  position: fixed;
  top: 1em;
  right: 1em;
}

.circular-menu .floating-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.5em;
  height: 3.5em;
  border-radius: 50%;
  background-color: hsl(175, 63%, 55%);
  box-shadow: 0 2px 5px 0 hsla(0, 0%, 0%, .26);  
  color: hsl(0, 0%, 100%);
  text-align: center;
  line-height: 3.9;
  cursor: pointer;
  outline: 0;
  margin-right: 20px;
}

.circular-menu.active .floating-btn {
  box-shadow: inset 0 0 3px hsla(0, 0%, 0%, .3);
}

.circular-menu .floating-btn:active {
  box-shadow: 0 4px 8px 0 hsla(0, 0%, 0%, .4);
}

.circular-menu .floating-btn i {
  font-size: 1.3em;
  transition: transform .2s;  
}

.circular-menu.active .floating-btn i {
  transform: rotate(150deg); /* Updated rotation angle */
}

.circular-menu:after {
  display: block;
  content: ' ';
  width: 3.5em;
  height: 3.5em;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -2;
  background-color:  hsl(175, 63%, 55%);
  transition: all .3s ease;
  margin-right: 20px;
}

.circular-menu.active:after {
  transform: scale3d(5.5, 5.5, 1);
  transition-timing-function: cubic-bezier(.68, 1.55, .265, 1);
}

.circular-menu .items-wrapper {
  padding: 0;
  margin: 0;
}

.circular-menu .menu-item {
  position: absolute;
  top: .2em;
  right: .2em;
  left: .2em;
  z-index: -1;
  display: flex; /* Center contents horizontally and vertically */
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: hsl(0, 0%, 100%);
  font-size: 1em;
  width: 3em;
  height: 3em;
  border-radius: 50%;
  text-align: center;
  line-height: 3;
  background-color: hsla(0,0%,0%,.1);
  transition: transform .3s ease, background .2s ease;
  margin: 5px; /* Added margin for symmetrical spacing */
}

.circular-menu .menu-item:hover {
  background-color: hsla(0,0%,0%,.3);
}

.circular-menu.active .menu-item {
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.circular-menu.active .menu-item:nth-child(1) {
  transform: translate3d(-8.5em,0,0); /* Updated rotation angle */
  margin-left:  20px;
}

.circular-menu.active .menu-item:nth-child(2) {
  transform: translate3d(-5.5em,3.0em,0); /* Updated rotation angle */
}

.circular-menu.active .menu-item:nth-child(3) {
  transform: translate3d(-3.0em,5.9em,0); /* Updated rotation angle */
}

.circular-menu.active .menu-item:nth-child(4) {
  transform: translate3d(0.5em,7.2em,0); /* Updated rotation angle */
}
